import React from 'react';
import { graphql } from 'gatsby';
import { GatsbySeo } from 'gatsby-plugin-next-seo';

import Layout from '../components/Layout';
import Container from '../components/Container';
import Footer from '../components/Footer';
import BlockLogos from '../components/BlockLogos';
import SpaceDivider from '../components/SpaceDivider';
import BlockContactUsHubspotForm from '../components/BlockContactUsHubspotForm';

import { edgesByName } from '../utils/gql';

export const pageQuery = graphql`
  {
    companies: allContentfulCompany {
      edges {
        node {
          name
          image {
            fluid(maxWidth: 150) {
              ...GatsbyContentfulFluid
            }
          }
        }
      }
    }
  }
`;

export default function ContactSalesPage({ data }) {
  const companies = edgesByName(data.companies, node => node.image.fluid);

  return (
    <Layout landingNav>
      <GatsbySeo title="Contact sales" />
      <Container>
        <BlockContactUsHubspotForm />
        <SpaceDivider height={100} />
        <BlockLogos
          pretitle="Trusted by innovative companies"
          data={[
            {
              image: companies['Vice Golf'],
              name: 'Vice Golf',
              offsetTop: 0,
            },
            {
              image: companies['Article'],
              name: 'Article',
            },
            {
              image: companies['OnDemand Tires'],
              name: 'OnDemand Tires',
              offsetTop: 0,
            },
            {
              image: companies['Archipelago Lighting'],
              name: 'Archipelago Lighting',
              offsetTop: 0,
            },
            {
              image: companies['Linus Bike'],
              name: 'Linus Bike',
              offsetTop: 0,
            },
            {
              image: companies['QuietKat'],
              name: 'QuietKat',
              offsetTop: 0,
            },
          ]}
        />
      </Container>
      <Footer darkSkin />
    </Layout>
  );
}
